import React from 'react'
import { CartItemBlock } from '../components';
import { useHistory } from 'react-router-dom';
import { useSelector,useDispatch } from 'react-redux';
import { clearCart, removeCartItem, plusCartItem, minusCartItem } from '../redux/actions/cart';
import style from '../scss/cartWithItems.module.scss';

function CartWithItems() {
    const { totalCount, totalPrice, items } = useSelector(({cart}) => cart);
    const dispatch = useDispatch();
    const history = useHistory();

    const addedItems = Object.keys(items).map(key => {
        return items[key].items[0];
    });
    const onClearCart = () => {
        if (window.confirm('–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–µ—Ç–∏–µ –æ—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É?')){
            dispatch(clearCart())
        }
    }
    const onRemoveItem = (id) => {
        if (window.confirm('–í—ã –¥–µ–π—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ —Ö–æ—Ç–µ—Ç–∏–µ —É–¥–∞–ª–∏—Ç—å?')){
            dispatch(removeCartItem(id))
        }
    }
    const onPlusItem = (id) =>{
        dispatch(plusCartItem(id))
    }
    const onMinusItem = (id) =>{
        dispatch(minusCartItem(id))
    }

    const hendleGoBack = () => {
        history.goBack()
    }

    return (
        <section className={style.cart_with_items}>
            <h2 className={style.cart}>–ö–æ—Ä–∑–∏–Ω–∞</h2>
            <h5 onClick={onClearCart} className={style.clear_cart}>–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É</h5>
            <div className={style.product}>
            {addedItems.map((obj) =>(
                <CartItemBlock 
                    key={obj.id}
                    id={obj.id}
                    img={obj.img}
                    name={obj.name} 
                    description={obj.description} 
                    price={obj.price} 
                    totalPrice={items[obj.id].totalPrice}
                    totalCount={items[obj.id].items.length}
                    onRemove={onRemoveItem}
                    onPlus={onPlusItem}
                    onMinus={onMinusItem}
                />))
            }
            </div>
            <h5 className={style.total_count}>
                –í—Å–µ–≥–æ —Ç–æ–≤–∞—Ä–æ–≤: {totalCount}
            </h5>
            <h5 className={style.total_price}>
                –°—É–º–º–∞ –∑–∞–∫–∞–∑–∞: {totalPrice} —Ä 
            </h5>
            <button 
                className={`${style.btn} ${style.back}`} 
                onClick={hendleGoBack}
            > 
                –í–µ—Ä–Ω—É—Ç—å—Å—è –Ω–∞–∑–∞–¥
            </button>
            <button className={`${style.btn} ${style.pay}`} onClick={() => alert("–û–ø–ª–∞—Ç–∞")}>
                –û–ø–ª–∞—Ç–∏—Ç—å —Å–µ–π—á–∞—Å
            </button>
        </section >
    )
}

export default CartWithItems